<template>
    <div class="city-ranking-router-container">
        <div >
            <div class="city-ranking-router-body">
                <div class="my-tab-header">
                    <a
                        href="javascript:;"
                        :class="activeTab == '2' ? 'active' : ''"
                        class="tab-btn"
                        @click="tabClick('2')"
                    >
                        日
                    </a>
                    <a
                        href="javascript:;"
                        :class="activeTab == '4' ? 'active' : ''"
                        class="tab-btn"
                        @click="tabClick('4')"
                    >
                        月
                    </a>
                    <a
                        href="javascript:;"
                        :class="activeTab == '5' ? 'active' : ''"
                        class="tab-btn"
                        @click="tabClick('5')"
                    >
                        年
                    </a>
                    <div class="now-date">
                        <span  v-if="activeTab === '2'">{{ day }}</span>
                        <span  v-else-if="activeTab === '4'">{{
                            month
                        }}</span>
                        <span   v-else>{{ year }}</span>
                    </div>
                    <div class="arrow-box">
                        <div class="arrow arrow-left" @click="reduceBtn" style="margin-right:25px;">
                            <img src="../../assets/reduce-icon.png" alt="">
                        </div>
                        <div class="arrow arrow-right" @click="addBtn">
                            <img src="../../assets/add-icon.png" alt="">
                        </div>
                    </div>
                </div>
                <!-- 表格 -->
                <AreaRanking :tableData="tableData" :modelShow="modelShow" />
            </div>
        </div>
    </div>
</template>

<script>
import AreaRanking from "./AreaRanking";
import { http } from "@/utils/request";

export default {
    props:['modelShow'],
    components: {
        AreaRanking,
    },
    data() {
        return {
            activeTab: "2",
            year: null,
            month: null,
            day: null,

            tableData:[]
        };
    },
    watch:{
        'modelShow'(newVal,oldVal){
            this.modelShow = newVal
            this.activeTab = '2'
            this.getWorkDataList(this.activeTab,this.day,this.modelShow)
        }
    },
    created() {
        this.getNowDate()
        if(this.modelShow){
            this.getWorkDataList(this.activeTab,this.day,this.modelShow)
        }
    },
    methods: {
        getNowDate(){
            this.year = this.$moment().format('YYYY')
            this.month = this.$moment().format('YYYY-MM')
            this.day = this.$moment().subtract(1, 'days').format('YYYY-MM-DD')
        },
        tabClick(activeTab) {
            this.activeTab = activeTab;
            let time;
            if(this.activeTab == '2'){
                time=this.day
            }else if(this.activeTab == '4'){
                time=this.$moment(this.month).format('YYYY-MM-DD')
            }else if(this.activeTab == '5'){
                time=this.$moment(this.year).format('YYYY-MM-DD')
            }
            this.getWorkDataList(this.activeTab,time,this.modelShow)
        },
        reduceBtn() {
            if (this.activeTab == "2") {
                this.day = this.$moment(this.day).subtract(1, 'days').format('YYYY-MM-DD')
                this.getWorkDataList(this.activeTab,this.day,this.modelShow)
            } else if (this.activeTab == "4") {
                this.month = this.$moment(this.month).subtract(1, 'month').format('YYYY-MM')
                let time = this.$moment(this.month).format('YYYY-MM-DD')
                this.getWorkDataList(this.activeTab,time,this.modelShow)
            } else {
                this.year = this.$moment(this.year).subtract(1, 'year').format('YYYY')
                let time = this.$moment(this.year).format('YYYY-MM-DD')
                this.getWorkDataList(this.activeTab,time,this.modelShow)
            }
        },
        addBtn() {
            if (this.activeTab == "2") {
                this.day = this.$moment(this.day).add(1, 'days').format('YYYY-MM-DD')
                this.getWorkDataList(this.activeTab,this.day,this.modelShow)
            } else if (this.activeTab == "4") {
                this.month = this.$moment(this.month).add(1, 'month').format('YYYY-MM')
                let time = this.$moment(this.month).format('YYYY-MM-DD')
                this.getWorkDataList(this.activeTab,time,this.modelShow)
            } else {
                this.year = this.$moment(this.year).add(1, 'year').format('YYYY')
                let time = this.$moment(this.year).format('YYYY-MM-DD')
                this.getWorkDataList(this.activeTab,time,this.modelShow)
            }
        },

        // 获取工作排名数据
        getWorkDataList(time_type,time,rank_type) {
            // time_type: 2: 日 4: 月 5: 年
            // time: 如果是月，是月的第1天。如果是年，是年的第1天
            // rank_type: city: 市 county: 区

            let json = {
                username: "api",
                 password: "a123456b",
                 time_type: time_type,
                 time: time,
                 rank_type: rank_type,
            }
            http.get("https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/ranks", { params:{
                json
            } }).then((res) => {
                if (res.data.code === 2000) {
                    this.tableData = res.data.result
                    if(rank_type == 'city'){
                        this.tableData = res.data.result.filter(item=>{
                            return item.name !=='北京市'
                        })
                    }
                } 
            });
        },
    },
};
</script>

<style lang="scss" >
.city-ranking-router-container {
    // width: 2421px;
    height: 969px;
    box-sizing: border-box;
    .main-header {
        position: relative;
    }
    .city-ranking-router-body {
        margin-top: 44px;
        // width: 2427px;
        height: 1130px;
        background: linear-gradient(
            180deg,
            rgba(14, 33, 90, 0.4) 0%,
            #0d1a36 100%
        );
        .my-tab-header {
            padding: 30px 94px 35px 0;
            display: flex;
            .tab-btn {
                width: 170px;
                height: 81px;
                line-height: 81px;
                text-align: center;
                background: #144588;
                font-size: 21px;
                color: #fff;
                &.active {
                    background: #4282da;
                }
            }
            .now-date {
                font-size: 21px;
                color: #fff;
                width: 550px;
                height: 81px;
                line-height: 81px;
                text-align: center;
                background: rgba(11, 55, 115, 0.57);
            }
            .arrow-box {
                width: 270px;
                height: 81px;
                line-height: 81px;
                text-align: center;
                background: rgba(11, 55, 115, 0.57);
                display: flex;
                justify-content: center;
                align-items: center;
                .arrow {
                    width: 57px;
                    height: 27px;
                    cursor: pointer;
                    img{
                        display: block;
                    }
                }
            }
        }
    }
}
</style>